/**
* 证据库组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="form-wrop">
            <lj-search-form :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                @selection-change="handleSelectionChange"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column align="center" label="案件名称" prop="caseName" show-overflow-tooltip sortable/>
            <el-table-column align="center" label="案件类型" prop="caseType" sortable width="120">
                <template slot-scope="scope" v-if="scope.row.caseType">{{scope.row.caseType | caseType}}</template>
            </el-table-column>
            <el-table-column align="center" label="文书名称" prop="typeName" sortable/>
            <el-table-column align="center" label="上传时间" prop="uploadTime" sortable width="120">
                <template
                        slot-scope="scope"
                        v-if="scope.row.uploadTime"
                >{{scope.row.uploadTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="备注" prop="remark" sortable/>
            <el-table-column align="center" label="操作" width="50">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                class="el-icon-view btn-margin"
                                type="success"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getAccountList"
                class="i-page-top"
        />

        <!-- 查看 -->
        <see-content :form="seeObj.form" :show.sync="seeObj.show" :title="seeObj.title"></see-content>
    </div>
</template>

<script>
    import {apiEvidenceSearch} from "../../../api/case";
    import LjPagination from "../../../components/LjPagination";
    import LjSearchForm from "../../../components/LjSearchForm";
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import seeContent from "./seeContent";

    let that;
    export default {
        name: "evidencePage",
        data() {
            return {
                loading: true,
                //查看数据
                seeObj: {
                    show: false,
                    title: "",
                    form: {}
                },
                caseTypeArr: [],
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                input: "",
                list: [],
                rowdata: {},
                idarr: [],
                arrstr: ""
            };
        },
        components: {
            LjPagination,
            LjSearchForm,
            seeContent
        },

        methods: {
            //表格批量选择
            handleSelectionChange(valarr) {
                let arr = [];
                valarr.forEach(item => {
                    arr.push(item.cvid);
                });
                let arrstr = arr.join(",");
                this.arrstr = arrstr;
            },

            //查询+搜索
            getAccountList() {
                this.loading = true;
                apiEvidenceSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },

            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = "证据详情查看";
                this.seeObj.form = row;
            },

            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                apiEvidenceSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },
        //页面创建前的钩子
        beforeCreate: function () {
            that = this;
        },
        //过滤器
        filters: {
            //性别判断
            ifsex(val) {
                return IFSEX[val];
            },
            //时间戳转换
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd");
            },
            //案件类型
            caseType(value) {
                if (
                    that.caseTypeArr.find(i => {
                        return i.dictValue == value;
                    })
                ) {
                    return that.caseTypeArr.find(i => {
                        return i.dictValue == value;
                    }).dictName;
                }
            }
        },
        computed: {},
        mounted() {
            //案件类型
            this.$until.workBookApi("caseType").then(res => {
                this.caseTypeArr = res;
            });
        },

        created() {
            this.getAccountList();
        }
    };
</script>

<style scoped>
    .form-wrop {
        margin-bottom: 10px;
    }

    .btn-margin {
        margin: 0 10px;
    }

    .i-page-top {
        margin-top: 10px;
    }
</style>
