<template>
    <!--
      /**
    * 证据库详情
    * @desc 查看证据详情
    * @author 乔亚军
    * @date 2019年6月3日17:22:43
    * @param {Object} [title]    - 参数说明
    * @param {String} [columns] - 参数说明
    * @example 调用示例
    **/
     -->
    <el-dialog
            :close-on-click-modal="false"
            :title="title"
            :visible="show"
            @close="closeDialog"
            append-to-body
            top="50px"
            width="750px"
    >
        <div class="detail-block" v-loading="loading">
            <div class="enforceRight-block">
                <el-row>
                    <!-- <el-col :span="24">
                                  <div>
                                      <img :src="imgsrc" alt="">
                                  </div>
                    </el-col>-->
                    <el-col :span="24">
                        <table class="gridTable">
                            <tr>
                                <td class="label">案件名称：</td>
                                <td colspan="4">
                                    <span>{{form.caseName}}</span>
                                </td>
                            </tr>
                            <tr v-if="form.partyType!=2">
                                <td class="label">案件类型：</td>
                                <td>
                                    <span>{{form.caseType|caseType}}</span>
                                </td>
                                <td class="label">文书名称：</td>
                                <td>
                                    <span>{{form.typeName}}</span>
                                </td>
                            </tr>

                            <tr>
                                <td class="label">上传时间：</td>
                                <td>
                                    <span>{{form.uploadTime|formatDate}}</span>
                                </td>
                                <td class="label">备注：</td>
                                <td>
                                    <span>{{form.remark}}</span>
                                </td>
                            </tr>
                            <tr>
                                <td class="label">图片地址：</td>
                                <td colspan="4">
                  <span class="fontcss">
                    <a :href="imgsrc" target="_blank">{{imgsrc}}</a>
                  </span>
                                </td>
                            </tr>
                        </table>
                    </el-col>
                </el-row>
            </div>
            <!-- <el-divider content-position="right" >
                      上传时间：{{form.uploadTime | formatDate}}

            </el-divider>-->
        </div>
    </el-dialog>
</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";
    import {seeImgApi} from "../../../api/case";

    let that;
    export default {
        name: "seeContent",
        props: {
            form: {
                type: Object,
                default: null
            },
            show: {
                type: Boolean,
                default: true
            },
            title: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                caseTypeArr: [],
                loading: true,
                imgsrc: ""
            };
        },
        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //弹框弹出时获取data给form
            initPage() {
                // window.open(seeImgApi+'c4231fcce5764a80a0d566c315029d7f')
                this.imgsrc = seeImgApi + this.form.caseId;
                this.loading = false;
                this.$until.workBookApi("caseType").then(res => {
                    this.caseTypeArr = res;
                });
            }
        },
        //页面创建前的钩子
        beforeCreate() {
            that = this;
        },

        //过滤器
        filters: {
            //是否判断
            YESORNO(val) {
                return YESORNO[val];
            },

            //时间戳
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd");
            },

            //案件类型
            caseType(value) {
                if (
                    that.caseTypeArr.find(i => {
                        return i.dictValue == value;
                    })
                ) {
                    return that.caseTypeArr.find(i => {
                        return i.dictValue == value;
                    }).dictName;
                }
            }
        },

        // //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold;
    }

    .casewrop {
        display: flex;
        justify-content: space-between;
        font-weight: 12px;
        border-top: 1px solid gray;
        margin-top: 10px;

        .case {
            width: 100%;
            height: 100px;

            p {
                width: 200px;
                margin: 10px;
            }
        }
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: 800;
        // display: inline-block;min-width: 100px;

        padding: 0 10px;
        // border-bottom: 1px dashed #C5C5C5
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 15px 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
